@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,600;0,700;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=IM+Fell+English+SC&family=Italianno&family=Lobster&family=PT+Sans&family=Qahiri&family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Architects+Daughter&family=Permanent+Marker&family=Roboto+Mono:ital@1&display=swap);
*{
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-family: 'Poppins';
  
}


.container{
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  
  
}


input {
  all: unset;
  
  font-size: 1em;
   border-radius: 15px;
  background-color: #FFB344;
  text-align: center;
   margin: 1em 1em 0 1em;
  width: 10em;
  color: white;
}


.addtask {
  
  margin: 0 auto;
  box-sizing: border-box;
  width: 400px;
  border-radius: 15px;
  
  
  position: relative;
  z-index: 3;
  margin: 0.5em;
  background-color:#79B4B7;
  height: 180px;
  
}

.addtask:hover {
  box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.25);
  transition: tansfrom 1s;
  transition: all 0.3s;
}

.lettert{
    width: 1.5em;
    height: 7em;
    background-color: #FEF5ED ;
    position: absolute;
    border-radius: 0 0 15px 15px;
    left: 5.3em;
    top: 2.50em;
    z-index: -1;
}

.lettero{
    width: 3.5em;
    height: 3.5em;
    background-color: transparent;
    border: 17px solid #FEF5ED;
    border-radius: 50%;
    position: absolute;
    top: 4em;
    left: 7em;
    
    
}

.letterd{
    font-size: 9em;
    position: absolute;
    color: #FEF5ED;
    right: 90px;
    top: 100px;
    line-height: 0;
    

}

.letteroo{
    font-size: 10em;
    position: absolute;
    
    right: 7px;
    top: 90px;
    line-height: 0;
}

.none{
    display: none;
}

.submit{
    all: unset;
    position: absolute;
    top: 11px;
    left: 11em;
    width: 2em;
    height: 2em;
    background-color: #00A19D;
    border-radius: 50%;

    display: flex;
    align-items: center;
    justify-content: center;
    
    cursor: pointer;
}

.plus {
    display: block;
    position: relative;
    width: 1.2em;
    height: 2px;
    background-color: transparent;
    pointer-events: none;



}

.plus::after {
    content: '';
    position: absolute;
    width: 1.2em;
    height: 2px;
    background-color: white;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);

}

.plus:before {
    content: '';
    position: absolute;
    width: 1.2em;
    height: 2px;
    background-color: white;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.title{
    display: none;
}

@media only screen and (max-width: 600px) {
  
    .lettert, .lettero, .letterd {
        display: none;
    }
    .addtask{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: row;
    }

    .letteroo{
        font-size: 4em;
        
        
        
    }
    .submit{
       all: unset;
       display: flex;
       justify-content: center;
       align-items: center;
       background-color: red;
       width: 3em;
       height: 3em;
        margin: 0.5em auto;
       background-color: #00A19D;
       border-radius: 50%;
      
       
    }

    .title{
        padding-top: 0.5em;
        color: white;
        text-align: center;
        font-size: 3em;
        display: block;
        line-height: 0.5em;
    }

    input{
        font-size: 1.5em;
        
    }
    .card div {
        font-size: 0.75rem;
    }

    
  }
.bebas {
    font-family: 'Bebas Neue', cursive;
}

.im {
    font-family: 'Abril Fatface', cursive;
}

.it {
    font-family: 'Roboto Mono', monospace;
}

.lobster {
    font-family: 'Lobster', cursive;
}

.pt {
    font-family: 'PT Sans', sans-serif;
}

.qahiri {
    font-family: 'Architects Daughter', cursive;
}

.roboto {
    font-family: 'Abril Fatface', cursive;
}

.completed {
    text-decoration: line-through;

}

.notcompleted {
    text-decoration: none;
}

.taskcard {
    background-color: red;
}


.container {

    background: linear-gradient(to bottom left, #65dfc9, #6cdbeb);
    display: flex;
    width: 100%;
    margin: 0 auto;


}


.transparent {
    background: white;
    width: 75%;
    min-height: 75vh;
    background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));
    display: flex;
    flex-wrap: wrap;
    padding: 1.5em;
    border-radius: 30px;
    margin: 2em;
    -webkit-backdrop-filter: blur(20px);
            backdrop-filter: blur(20px);

}

.circle {
    position: absolute;
    width: 15em;
    height: 15em;
    border-radius: 50%;
    background: white;
    background-color: red;
    top: -4em;
    left: -4em;
    background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.01), rgba(255, 255, 255, 0.9));


}

.circle2 {
    position: absolute;
    width: 15em;
    height: 15em;
    border-radius: 50%;
    background: white;
    background-color: red;
    bottom: -4em;
    right: -4em;
    background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.1));
}

.card {
    min-width: 150px;
    align-self: auto;
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: calc(33.33% -5px);
    margin: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, .25);
    transition: all .3s ease-in-out;
    position: relative;
    
}



.card:hover {
    box-shadow: 0px 5px 100px rgba(0, 0, 0, .25);
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: tansfrom 1s;
    transition: all .3s;
}

.card div {
    padding: 1em;

}

.card div p {
    color: white;
    font-size: 1.5rem;
}

.delete {
    all: unset;
    position: absolute;
    right: -5px;
    top: -5px;
    width: 2em;
    height: 2em;
    background-color: white;
    border-radius: 50%;
    background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.9));
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.delete:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: all .3s;
}

.cross {
    display: block;
    position: relative;
    width: 1.2em;
    height: 2px;
    background-color: transparent;
    pointer-events: none;



}



.cross::after {
    content: '';
    position: absolute;
    width: 1.2em;
    height: 2px;
    background-color: black;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);

}

.cross:before {
    content: '';
    position: absolute;
    width: 1.2em;
    height: 2px;
    background-color: black;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}


@media only screen and (max-width: 600px) {
    .transparent {
      width: 100%;
    }
    .card div p {
        font-size: 1.3rem;

    }
    .delete{
        width: 2.5em;
        height: 2.5em
    }
    .cross::after, .cross::before{
        width: 1.3em;
    }
    .transparent{
        padding-top: 1em;
    }
  }
